<template>
	<view>
		<navbar index='mine' title="我的"></navbar>
		<view class="my-header">
			<view class="my-header_face">
				<view class="my-header_facebox" @click="updateFace">
					<image :src="imgUrl"></image>
				</view>
				<view class="my-header_msg">
					<text class="my-header_name">Tom</text>
					<text class="my-header_des">个人描述</text>
				</view>

				<view class="act">
					<view class="act_QR" @click="open_QR">
						<text class="iconfont icon-ico" style="font-size: 25px;"></text>
					</view>
					<view class="act_setting" @click="open_setting">
						<uni-icons type="gear" size="25"></uni-icons>
					</view>
				</view>
			</view>
			<view class="my-header_hr"></view>
			<view class="my-header_info">
				<view class="my-header_infobox">
					<text class="my-header_infotitle">关注</text>
					<text>123</text>
				</view>
				<view class="my-header_infobox">
					<text class="my-header_infotitle">粉丝</text>
					<text>123</text>
				</view>
				<view class="my-header_infobox">
					<text class="my-header_infotitle">信用</text>
					<text>123</text>
				</view>

			</view>
		</view>
		<view class="my-function">
			<view class="my-function_list">
				<view class="my-function_box">
					<text class="iconfont icon-geren"></text>
					<text>个人信息</text>
				</view>
				<view class="my-function_box">
					<text class="iconfont icon-iconfontzhizuobiaozhun23"></text>
					<text>我的收藏</text>
				</view>
				<view class="my-function_box">
					<text class="iconfont icon-wenjian"></text>
					<text>我的文件</text>
				</view>
			</view>
			<view class="my-function_list">
				<view class="my-function_box">
					<text class="iconfont icon-jifen"></text>
					<text>我的积分</text>
				</view>
				<view class="my-function_box">
					<text class="iconfont icon-wj-jh"></text>
					<text>我的计划</text>
				</view>
				<view class="my-function_box" @click="open_help">
					<text class="iconfont icon-help"></text>
					<text>帮助与反馈</text>
				</view>
			</view>
			
		</view>
		<view class="my-countdown">
			<countlist v-for="items in 4"></countlist>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				imgUrl:'http://ww1.sinaimg.cn/large/005IpECBly1gnna6qrzywj30ku0kgwpl.jpg'
			}
		},
		methods: {
			open_setting() {
				uni.navigateTo({
					url: '/pages/mine-setting/mine-setting'
				})
			},
			open_help(){
				uni.navigateTo({
					url: '/pages/mine-help/mine-help'
				})
			},
			open_QR(){
				uni.navigateTo({
					url: '/pages/mine-QR/mine-QR'
				})
			},
			updateFace(){
				uni.chooseImage({
					count: 1,
					success(res) {
						this.imgUrl = res.tempFilePaths[0]
						console.log(res.tempFilePaths[0])
					}
				})
			}
		}
	}
</script>

<style lang="scss">

	page {
		height: 100%;
		background-color: #f5f5f5;
	}

	.my-header {
		margin: 10px 0;
		background-color: #fff;
		padding-bottom: 10px;
		// border-top: 1px #999 solid;

		.my-header_face {
			display: flex;
			padding-top: 10px;
			.my-header_facebox {
				width: 60px;
				height: 60px;
				margin: 10px;
				border-radius: 50%;
				overflow: hidden;

				image {
					width: 100%;
					height: 100%;
				}
			}

			.my-header_msg {
				display: flex;
				flex-direction: column;
				margin-right: auto;
				margin-left: 10px;

				.my-header_name {
					margin-top: 10px;
					font-size: 16px;
					font-weight: bold;
				}

				.my-header_des {
					margin-top: 5px;
					font-size: 14px;
					color: #999;
				}
			}

			.act {
				display: flex;
				margin-top: 10px;

				.act_QR,
				.act_setting {
					margin-right: 10px;
				}
			}

		}

		.my-header_hr {
			border-bottom: 1px #dcdcdc solid;
			height: 1px;
			width: 95%;
			margin: 0 auto;
		}

		.my-header_info {
			margin-top: 10px;
			display: flex;

			.my-header_infobox {
				display: flex;
				justify-content: center;
				flex-direction: column;
				align-items: center;
				font-size: 12px;
				width: 100%;
				color: #999;

				.my-header_infotitle {
					font-size: 14px;
					color: #333;
				}
			}
		}
	}

	.my-function{

		.my-function_list {
			display: flex;
			background-color: #fff;
			.my-function_box {
				margin: 10px;
				display: flex;
				width: 100%;
				justify-content: center;
				flex-direction: column;
				align-items: center;
		
				.iconfont {
					margin-bottom: 5px;
					font-size: 26px;
				}
		
			}
		
		}
	}
	.my-countdown{
		margin-top: 10px;
		background-color: #fff;
	}
	
</style>
